/**
 * TELEX Vietnamese Input Method Engine
 * 
 * Simple implementation: when typing a modifier that already exists,
 * it toggles off (removes the diacritic/tone).
 */

import {
  isVietnameseVowel,
} from "../maps/vietnamese-chars";
import { findToneVowelIndex } from "../maps/vietnamese-syllable-rules";

/**
 * TELEX transformations for vowels
 * Maps base vowel + modifier key to result
 */
const VOWEL_TRANSFORMS: Record<string, Record<string, string>> = {
  a: { a: "â", w: "ă" },
  e: { e: "ê" },
  o: { o: "ô", w: "ơ" },
  u: { w: "ư" },
  A: { A: "Â", W: "Ă" },
  E: { E: "Ê" },
  O: { O: "Ô", W: "Ơ" },
  U: { W: "Ư" },
  
  // Reverse transforms (toggle off)
  â: { a: "a", w: "ă" },
  ă: { w: "a" },
  ê: { e: "e" },
  ô: { o: "o", w: "ơ" },
  ơ: { w: "o" },
  ư: { w: "u" },
  Â: { A: "A", W: "Ă" },
  Ă: { W: "A" },
  Ê: { E: "E" },
  Ô: { O: "O", W: "Ơ" },
  Ơ: { W: "O" },
  Ư: { W: "U" },
};

/**
 * TELEX transformations for consonants
 */
const CONSONANT_TRANSFORMS: Record<string, string> = {
  dd: "đ",
  DD: "Đ",
  Dd: "Đ",
};

/**
 * Tone mark mappings from TELEX keys to tone names
 */
const TELEX_TONE_MARKS: Record<string, string> = {
  s: "acute",    // sắc
  f: "grave",    // huyền
  r: "hook",     // hỏi
  x: "tilde",    // ngã
  j: "dot",      // nặng
};

/**
 * Vietnamese tone marks mapping
 * Maps tone names to vowel transformations
 */
const TONE_MARKS: Record<string, Record<string, string>> = {
  acute: {
    a: "á", e: "é", i: "í", o: "ó", u: "ú", y: "ý",
    ă: "ắ", â: "ấ", ê: "ế", ô: "ố", ơ: "ớ", ư: "ứ",
  },
  grave: {
    a: "à", e: "è", i: "ì", o: "ò", u: "ù", y: "ỳ",
    ă: "ằ", â: "ầ", ê: "ề", ô: "ồ", ơ: "ờ", ư: "ừ",
  },
  hook: {
    a: "ả", e: "ẻ", i: "ỉ", o: "ỏ", u: "ủ", y: "ỷ",
    ă: "ẳ", â: "ẩ", ê: "ể", ô: "ổ", ơ: "ở", ư: "ử",
  },
  tilde: {
    a: "ã", e: "ẽ", i: "ĩ", o: "õ", u: "ũ", y: "ỹ",
    ă: "ẵ", â: "ẫ", ê: "ễ", ô: "ỗ", ơ: "ỡ", ư: "ữ",
  },
  dot: {
    a: "ạ", e: "ẹ", i: "ị", o: "ọ", u: "ụ", y: "ỵ",
    ă: "ặ", â: "ậ", ê: "ệ", ô: "ộ", ơ: "ợ", ư: "ự",
  },
};

// Build a reverse map to strip tones while preserving vowel quality (â, ê, ô, ơ, ư, ă)
const REVERSE_TONE_MAP: Record<string, string> = (() => {
  const reverse: Record<string, string> = {};
  for (const tone of Object.values(TONE_MARKS)) {
    for (const [base, toned] of Object.entries(tone)) {
      reverse[toned] = base;
      reverse[toned.toUpperCase()] = base.toUpperCase();
    }
  }
  return reverse;
})();

/**
 * Apply TELEX transformations to input
 */
function applyTransformations(input: string): string {
  let result = "";
  
  for (let i = 0; i < input.length; i++) {
    const curr = input[i];
    const next = i + 1 < input.length ? input[i + 1] : "";
    const nextNext = i + 2 < input.length ? input[i + 2] : "";
    const nextNextNext = i + 3 < input.length ? input[i + 3] : "";
    

    
    // Check for uwow pattern first (4 characters)
    if (curr.toLowerCase() === 'u' && next.toLowerCase() === 'w' && 
        nextNext.toLowerCase() === 'o' && nextNextNext.toLowerCase() === 'w') {
      // Transform uwow → ươ
      if (curr === 'U') {
        result += 'Ươ';
      } else {
        result += 'ươ';
      }
      i += 3;
      continue;
    }
    
    // Check three-character pattern: uow → ươ
    if (curr.toLowerCase() === 'u' && next.toLowerCase() === 'o' && 
        nextNext.toLowerCase() === 'w') {
      // Preserve case
      if (curr === 'U' && next === 'O' && nextNext === 'W') {
        result += 'ƯƠ';
      } else if (curr === 'U') {
        result += 'Ươ';
      } else {
        result += 'ươ';
      }
      i += 2;
      continue;
    }
    
    // Check consonant transforms (dd → đ)
    if (i + 1 < input.length) {
      const twoChar = curr + next;
      if (CONSONANT_TRANSFORMS[twoChar]) {
        result += CONSONANT_TRANSFORMS[twoChar];
        i++;
        continue;
      }
    }
    
    // Check vowel transforms - simple toggle logic
    if (VOWEL_TRANSFORMS[curr] && VOWEL_TRANSFORMS[curr][next]) {
      result += VOWEL_TRANSFORMS[curr][next];
      i++;
    } else {
      result += curr;
    }
  }
  
  // Apply complex patterns that emerge from typing order
  result = applyComplexVowelPatterns(result);
    
  return result;
}

/**
 * Apply complex vowel patterns that emerge from typing order variations
 */
function applyComplexVowelPatterns(input: string): string {
  let result = input;
  
  // Handle patterns where users type modifier after vowel cluster
  // e.g., oiw → ơi, eue → êu, aua → âu, aya → ây
  result = result.replace(/oiw/gi, m => m[0] === 'O' ? 'Ơi' : 'ơi');
  result = result.replace(/eue/gi, m => m[0] === 'E' ? 'Êu' : 'êu');
  result = result.replace(/aua/gi, m => m[0] === 'A' ? 'Âu' : 'âu');
  result = result.replace(/aya/gi, m => m[0] === 'A' ? 'Ây' : 'ây');
  
  // Handle ieu → iêu, yeu → yêu
  result = result.replace(/ieu/gi, m => m[0] === 'I' ? 'Iêu' : 'iêu');
  result = result.replace(/yeu/gi, m => m[0] === 'Y' ? 'Yêu' : 'yêu');
  
  // Handle uye → uyê
  result = result.replace(/uye/gi, m => m[0] === 'U' ? 'Uyê' : 'uyê');
  
  // Handle uu → ưu
  result = result.replace(/uu/gi, m => m[0] === 'U' ? 'Ưu' : 'ưu');
  
  // Handle quality toggle: êe → ee, âa → aa, ôo → oo
  result = result.replace(/êe/gi, m => m[0] === 'Ê' ? 'Ee' : 'ee');
  result = result.replace(/âa/gi, m => m[0] === 'Â' ? 'Aa' : 'aa');
  result = result.replace(/ôo/gi, m => m[0] === 'Ô' ? 'Oo' : 'oo');
    
    return result;
  }

  /**
 * Apply long-distance transformations
   */
function applyLongDistanceTransforms(word: string): string {
    let result = word;
    
  // Long-distance dd transformation (e.g., did → đi)
  result = result.replace(/d([^d])d/gi, (match, middle) => {
    const isUpper = match[0] === 'D';
    return (isUpper ? 'Đ' : 'đ') + middle;
  });

  // Long-distance vowel transformations (e.g., lene → lên)
  // Match pattern: vowel + consonant + same vowel
  result = result.replace(/([aeiou])([^aeiouâêôơưăÂÊÔƠƯĂ])(\1)/gi, (match, v1, middle) => {
    const transforms = VOWEL_TRANSFORMS[v1.toLowerCase()];
    if (transforms && transforms[v1.toLowerCase()]) {
      // Keep original case
      const transformed = v1 === v1.toUpperCase() ? 
        transforms[v1.toLowerCase()].toUpperCase() : 
        transforms[v1.toLowerCase()];
      return transformed + middle;
    }
    return match;
  });
    
    return result;
  }

  /**
 * Extract tone marks from the word
 * Simple logic: collect ALL tone keys, process them in order
 */
function extractToneMarks(word: string): { base: string; tone: string | null; toneKey: string | null } {
  let base = word;
  let tone: string | null = null;
  let toneKey: string | null = null;

  // Handle 'z' for clearing diacritics
  if (base.includes('z') || base.includes('Z')) {
    // Find tone keys that might be after z
    const zIndex = base.toLowerCase().indexOf('z');
    const afterZ = base.slice(zIndex + 1);
    
    // Special case: if it's just a quality diacritic + z, keep the quality
    if (base.match(/^[âăêôơưÂĂÊÔƠƯ]z$/i)) {
      // Just remove the z
      base = base.replace(/z/gi, '');
      return { base, tone: null, toneKey: null };
    }
    
    // Otherwise, remove all diacritics and tones before z
    const beforeZ = base.slice(0, zIndex);
    const clearedBefore = [...beforeZ].map(ch => REVERSE_TONE_MAP[ch] || ch).join('');
    
    // Also handle quality diacritics
    let result = clearedBefore.replace(/[âăêôơư]/g, ch => {
      const map: Record<string, string> = {
        'â': 'a', 'ă': 'a', 'ê': 'e', 'ô': 'o', 'ơ': 'o', 'ư': 'u'
      };
      return map[ch] || ch;
    });
    result = result.replace(/[ÂĂÊÔƠƯ]/g, ch => {
      const map: Record<string, string> = {
        'Â': 'A', 'Ă': 'A', 'Ê': 'E', 'Ô': 'O', 'Ơ': 'O', 'Ư': 'U'
      };
      return map[ch] || ch;
    });
    
    // Don't include the z, but keep everything after it (including tone keys)
    base = result + afterZ;
    return { base, tone: null, toneKey: null };
  }

  // Collect tone keys that appear after vowels or at the end
  const chars = [...base];
  const toneKeys: { key: string; index: number }[] = [];
  let hasVowel = false;
  
  for (let i = 0; i < chars.length; i++) {
    const ch = chars[i];
    
    // Check if we've seen a vowel
    if (/[aeiouyâăêôơưAEIOUYÂĂÊÔƠƯ]/.test(ch)) {
      hasVowel = true;
    }
    
    // Only consider tone keys after we've seen a vowel (order-agnostic)
    if (hasVowel && /[sfrxjSFRXJ]/.test(ch)) {
      toneKeys.push({ key: ch.toLowerCase(), index: i });
    }
  }
  
  if (toneKeys.length > 0) {
    // Remove all tone keys from base
    base = chars.filter((_, i) => !toneKeys.some(tk => tk.index === i)).join('');
    
    // Check if base already has a tone
    let currentTone: string | null = null;
    for (const ch of base) {
      if (REVERSE_TONE_MAP[ch]) {
        // Find which tone this is
        for (const [toneName, toneMap] of Object.entries(TONE_MARKS)) {
          if (Object.values(toneMap).includes(ch.toLowerCase())) {
            currentTone = toneName;
            break;
          }
        }
        if (currentTone) break;
      }
    }
    
    // Process tone keys in order
    let resultTone: string | null = currentTone;
    let toggledOff = false;
    
    for (const tk of toneKeys) {
      const newTone = TELEX_TONE_MARKS[tk.key];
      
      if (resultTone === newTone) {
        // Toggle off
        resultTone = null;
        toggledOff = true;
      } else {
        // Apply new tone
        resultTone = newTone;
        toggledOff = false;
      }
    }
    
    if (toggledOff && toneKeys.length > 0) {
      // If toggled off, keep the last tone key as literal
      base = [...base].map(ch => REVERSE_TONE_MAP[ch] || ch).join('');
      base = base + toneKeys[toneKeys.length - 1].key;
      tone = null;
      toneKey = null;
    } else if (resultTone) {
      // Apply the final tone
      tone = resultTone;
      toneKey = toneKeys[toneKeys.length - 1].key;
    }
  }

  return { base, tone, toneKey };
}

/**
 * Apply tone to a word at the correct position
 */
function applyToneToWord(word: string, tone: string): string {
  // Find tone index after all transformations are complete
  const toneIndex = findToneVowelIndex(word);
  if (toneIndex === -1 || toneIndex >= word.length) return word;

  const chars = word.split('');
  const vowel = chars[toneIndex];
  
  if (!vowel || !isVietnameseVowel(vowel)) return word; // Safety check
  
  const toneMap = TONE_MARKS[tone];
  if (!toneMap) return word; // Invalid tone

  // Strip any existing tone while keeping quality diacritics (â, ê, ô, ơ, ư, ă)
  const baseQualityVowel = REVERSE_TONE_MAP[vowel] || vowel;
  const tonedVowel = toneMap[baseQualityVowel.toLowerCase()];

  if (tonedVowel) {
    // Preserve case
    chars[toneIndex] = baseQualityVowel === baseQualityVowel.toUpperCase() ? 
      tonedVowel.toUpperCase() : 
      tonedVowel;
  }

  return chars.join('');
}

/**
 * Process a single Vietnamese word with TELEX rules
 */
export function processWord(input: string): string {
  if (!input) return input;

  // Extract tone marks first
  const { base, tone } = extractToneMarks(input);

  // Pre-normalize modifiers: move any 'w' typed after the coda back to the end of the vowel nucleus
  const preNormalized = preNormalizeModifiers(base);

  // Apply long-distance transformations first
  let result = applyLongDistanceTransforms(preNormalized);

  // Apply regular transformations iteratively until stable (order-agnostic)
  let prev = "";
  let guard = 0;
  while (prev !== result && guard < 6) {
    prev = result;
    result = applyTransformations(result);
    
    // Check if a quality toggle occurred (êe→ee, âa→aa, ôo→oo)
    if (/[êâô]e|[âă]a|[ôơ]o/i.test(prev) && /ee|aa|oo/i.test(result)) {
      break; // Stop iterations after quality toggle
    }
    
    guard++;
  }
  
  // Special handling for ưow → ươ after iterations
  // This ensures uwow patterns are caught even after uw → ư transformation
  result = result.replace(/ưow/gi, m => m[0] === 'Ư' ? 'Ươ' : 'ươ');

  // Apply tone if present
  if (tone) {
    result = applyToneToWord(result, tone);
  }

  return result;
}

/**
 * Pre-normalize modifier placement to make order fully flexible.
 * - Move any 'w' characters that occur after the vowel nucleus to immediately
 *   after the end of the vowel nucleus so cluster transforms (e.g., uow→ươ)
 *   can be recognized regardless of typing order (e.g., nhungwx → những).
 */
function preNormalizeModifiers(word: string): string {
  // Skip pre-normalization for patterns that should remain intact
  // Check for uwow, uow patterns that shouldn't be normalized
  if (/u[wo]ow/i.test(word)) {
    return word; // Don't normalize uwow or uow patterns
  }
  
  // Find the vowel sequence (nucleus) span
  const vowelsInfo = ((): { start: number; end: number } | null => {
    // Reuse logic similar to extractVowelSequenceSkippingQu but return end index
    let startIndex = -1;
    let endIndex = -1;
    for (let i = 0; i < word.length; i++) {
      const ch = word[i];
      const prev = i > 0 ? word[i - 1] : "";
      const isQuU = (ch === 'u' || ch === 'U') && (prev === 'q' || prev === 'Q');
      if (isVietnameseVowel(ch) && !isQuU) {
        if (startIndex === -1) startIndex = i;
        endIndex = i; // extend nucleus to include contiguous vowels
      } else if (startIndex !== -1) {
        break; // nucleus ended
      }
    }
    return startIndex !== -1 ? { start: startIndex, end: endIndex } : null;
  })();

  if (!vowelsInfo) return word;

  const { start, end } = vowelsInfo;

  // Collect exact sequence of 'w'/'W' appearing after the nucleus (preserve case)
  let collected = '';
  for (let i = end + 1; i < word.length; i++) {
    if (word[i] === 'w' || word[i] === 'W') collected += word[i];
  }
  if (collected.length === 0) return word;

  // Remove all 'w'/'W' after the nucleus
  const withoutTrailingW =
    word.slice(0, end + 1) + word.slice(end + 1).replace(/[wW]+/g, '');

  // Decide the best insertion point for 'w' relative to the nucleus
  const nucleus = withoutTrailingW.slice(start, end + 1);
  let insertPos = end + 1; // default: right after nucleus
  const lowerNucleus = nucleus.toLowerCase();
  // Prefer placing after 'o' in a 'uo' nucleus to form 'uow'
  const uoIndex = lowerNucleus.indexOf('uo');
  if (uoIndex !== -1) {
    insertPos = start + uoIndex + 2; // after the 'o'
  }

  // Insert the collected sequence at the chosen position (preserve original case)
  const normalized = withoutTrailingW.slice(0, insertPos) + collected + withoutTrailingW.slice(insertPos);
  return normalized;
}

/**
 * Main TELEX processing function
 */
export default function telex(input: string): string {
  // Process each word separately
  return input
    .split(/(\s+)/)
    .map((part) => {
      // Keep whitespace as-is
      if (/^\s+$/.test(part)) return part;
      return processWord(part);
    })
    .join('');
}

/**
 * TelexInputMethod class for compatibility with existing tests
 */
export class TelexInputMethod {
  private buffer: string = '';

  process(currentInput: string, key: string): string {
    // Special handling for z in incremental mode
    if (key.toLowerCase() === 'z') {
      // In incremental mode, z doesn't clear, just return current input
      this.buffer = currentInput;
      return this.buffer;
    }
    
    // Simulate incremental input
    this.buffer = currentInput + key;
    const result = processWord(this.buffer);
    
    // If the result is different, update the buffer
    if (result !== this.buffer) {
      this.buffer = result;
    }
    
    return this.buffer;
  }

  processWord(word: string): string {
    return processWord(word);
  }

  reset(): void {
    this.buffer = '';
  }
}